<template>
    <div class="radio" :class="{active:value == label,disabled:disabled}" @click="handleRadio">
        <span class="radio-icon"></span>
        <span class="radio-txt"><slot></slot></span>

        <!--禁用时候覆盖 防止能点击-->
        <span class="over" @click.stop></span>
    </div>
</template>
<script>
    export default {
        name: 'radio',
        props:{
            label:{
                type:[String,Number],
                default:null
            },
            value:{
                type:[String,Number],
                default:null
            },
            disabled:{
                type:Boolean,
                default:false
            }
        },
        data() {
            return {

            }
        },
        mounted(){
        },
        methods:{
            //单选项点击
            handleRadio(){
                this.$emit('input',this.label);
                this.$emit('change',this.label);
            }
        }
    }
</script>
<style scoped>
    .radio{
        height: 22px;overflow: hidden;line-height: 22px;float:left;cursor:pointer;
    }
    .radio-icon{width: 16px;height: 16px;float:left;display: block;
        background: url("../../assets/images/check1.jpg") no-repeat center;
        background-size: 100%;margin-top:3px;
    }
    .radio-txt{display: block;float:left;font-size: 14px;color:#333;margin-left:5px;margin-right: 10px;}
    .radio.active .radio-icon{background: url("../../assets/images/check1_active.png") no-repeat center;
        background-size: 100%;}

    .radio.disabled{opacity: 0.5;position: relative;}
    .over{position: absolute;top:0px;left:0px;right:0px;bottom:0px;display: none;}
    .radio.disabled .over{display: block;cursor: no-drop;}
</style>


