<template>
    <div class="wl-radio">
        <div class="h-headtitle">Radio 单选框</div>
        <div class="h-blocks">
            <div class="h-title">介绍</div>
            <div class="h-main">
                在一组备选项中进行单选
            </div>
        </div>

        <div class="h-blocks">
            <div class="h-title">基础用法</div>
            <div class="hb-a">由于选项默认可见，不宜过多。</div>
            <div class="hb-a">
                <Radio v-model="radio1" :label="1">单选1</Radio>
                <Radio v-model="radio1" :label="2">单选2</Radio>
            </div>
            <div class="hb-a">要使用 Radio 组件，只需要设置v-model绑定变量，选中意味着变量的值为相应 Radio label属性的值，label可以是String、Number。</div>
            <div class="hb-code">
                <div class="hb-cmain">
                    <div v-highlight>
                        <pre>
                            <code >
                &lt;Radio v-model="radio1" :label="1"&gt;单选1&lt;/Radio&gt;
                &lt;Radio v-model="radio1" :label="2"&gt;单选2&lt;/Radio&gt;
                            </code>
                        </pre>
                    </div>
                </div>
            </div>
            <div class="hb-b view-code-btn">查看代码</div>
        </div>

        <div class="h-blocks">
            <div class="h-title">禁用状态</div>
            <div class="hb-a">单选框不可用的状态。</div>
            <div class="hb-a">
                <Radio v-model="radio2" disabled label="禁用">单选1</Radio>
                <Radio v-model="radio2" disabled label="选中加禁用">单选2</Radio>
            </div>
            <div class="hb-a">只要在Radio元素中设置disabled属性即可，它接受一个Boolean，true为禁用。</div>
            <div class="hb-code">
                <div class="hb-cmain">
                    <div v-highlight>
                        <pre>
                            <code >
                &lt;Radio v-model="radio2" disabled label="禁用"&gt;单选1&lt;/Radio&gt;
                &lt;Radio v-model="radio2" disabled label="选中加禁用"&gt;单选2&lt;/Radio&gt;
                            </code>
                        </pre>
                    </div>
                </div>
            </div>
            <div class="hb-b view-code-btn">查看代码</div>
        </div>
        <div class="h-blocks">
            <div class="h-title">属性</div>
            <div class="hb-a">
                <el-table
                        :data="tableData"
                        style="width: 100%">
                    <el-table-column
                            prop="param"
                            label="参数"
                            width="180">
                    </el-table-column>
                    <el-table-column
                            prop="desc"
                            label="说明"
                            width="180">
                    </el-table-column>
                    <el-table-column
                            prop="type"
                            label="类型">
                    </el-table-column>
                    <el-table-column
                            prop="defaultVal"
                            label="默认值">
                    </el-table-column>
                </el-table>
            </div>
        </div>
        <div class="h-blocks">
            <div class="h-title">Events</div>
            <div class="hb-a">
                <el-table
                        :data="tableDataEvent"
                        style="width: 100%">
                    <el-table-column
                            prop="eventName"
                            label="事件名"
                            width="180">
                    </el-table-column>
                    <el-table-column
                            prop="desc"
                            label="说明"
                            width="180">
                    </el-table-column>
                    <el-table-column
                            prop="backParams"
                            label="回调参数">
                    </el-table-column>
                </el-table>
            </div>
        </div>
    </div>
</template>

<script>
    import Radio from '@/components/template/Radio.vue'
    export default {
        name: 'wlradio',
        data() {
            return {
                radio1:'1',
                radio2:'选中加禁用',
                tableData: [
                    {
                        param: 'v-model',
                        desc: '绑定值',
                        type: 'string / number',
                        defaultVal: '',
                    },
                    {
                        param: 'label',
                        desc: 'Radio 的 value',
                        type: 'string / number',
                        defaultVal: '',
                    },
                    {
                        param: 'disabled',
                        desc: '是否禁用',
                        type: 'boolean',
                        defaultVal: 'false',
                    }
                ],
                tableDataEvent: [
                    {
                        eventName: 'change',
                        desc: '绑定值变化时触发的事件',
                        backParams: '选中的 Radio label 值',
                    }
                ],
            }
        },
        mounted(){
            this.doCode();
        },
        methods:{
            //一进来需要执行的
            doCode(){
                $('.view-code-btn').click(function (){
                    $(this).parent().find('.hb-code').slideToggle();
                });
            },
        },
        components:{
            Radio
        }
    }
</script>
<style scoped>

</style>



